.movie__descr {
    font-family: 'Roboto';

    margin-top: 8px;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1,1;
}
.movie__title {    
    color: var(--black-text-color);
}
.movie__descr--orange {
    display: flex;

    margin-top: 2px;

    color: var(--orange);
    
}
.movie__vote {
    display: flex;
    justify-content: center;
    align-items: center;
    
    margin-left: 16px;

    width: 36px;
    height: 16px;

    border-radius: 5px;

    background-color: var(--orange);
    color: var(--white-text-color);
    
}
