@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.spinner {
  animation-name: spin;
  animation-duration: 3s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.spinner__container {
  width: 100%;
  height: 100%;
  z-index: 10000000;
  position: absolute;
}

.spinner__container.is-hidden .spinner__backdrop {
  opacity: 0;
  pointer-events: none;
  z-index: -10;
}

.spinner__backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(255, 255, 255, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
}

.spinner__box {
  width: 50px;
  height: 50px;
  background-color: rgba(0, 0, 0, 0);
  margin: 0 auto;
  padding: 20px 0 0;
}

